import React, { useEffect, useState } from 'react';
import axios from 'axios';
import Loader from '../Loader/Loader';
import ImageGalleryItem from '../ImageGalleryItem/ImageGalleryItem';
import styles from './ImageGallery.module.css';

export default function ImageGallery({ query }) {
  const [images, setImages] = useState([]); // Starea pentru a stoca imaginile
  const [page, setPage] = useState(1); // Starea pentru a stoca numrul paginii curente
  const [isLoading, setIsLoading] = useState(false); // Starea pentru a indica dac se ncarc datele
  const [error, setError] = useState(null); // Starea pentru a stoca mesajele de eroare

  // Funcia pentru a face cererea API i a obine imaginile
  const fetchImages = async (typedQuery, pageNum) => {
    const url = `https://pixabay.com/api/?key=49093767-f763ba3dd9ac650ddc614c870&q=${typedQuery}&page=${pageNum}&per_page=12&orientation=horizontal`;
    console.log(`Fetching images with URL: ${url}`); // Adaug un console log pentru a verifica URL-ul
    setIsLoading(true); // Seteaz starea de ncrcare la true
    try {
      const response = await axios.get(url);
      console.log('Response:', response); // Adaug un console log pentru a verifica rspunsul
      const { data } = response;
      const { totalHits, hits } = data;
      if (totalHits === 0) {
        setError(
          'Nu a fost gasita nicio imagine care corespunde criteriului de cautare.'
        );
        setIsLoading(false);
        return;
      }
      setImages(prevImages => [...prevImages, ...hits]); // Adaug imaginile noi la cele existente
    } catch (error) {
      console.error('Error fetching images:', error); // Adaug un console log pentru a verifica eroarea
      setError('A aparut o eroare la cererea imaginilor de pe server.');
      setIsLoading(false);
    } finally {
      setIsLoading(false); // Seteaz starea de ncrcare la false
    }
  };

  // useEffect pentru a face cererea API atunci cnd query se schimb
  useEffect(() => {
    if (query) {
      setImages([]); // Reseteaz imaginile cnd query se schimb
      setPage(1); // Reseteaz pagina la 1 cnd query se schimb
      fetchImages(query, 1); // Face cererea API cu query i pagina 1
    }
  }, [query]);

  // useEffect pentru a face cererea API atunci cnd pagina se schimb
  useEffect(() => {
    if (page > 1) {
      fetchImages(query, page); // Face cererea API cu query i pagina curent
    }
  }, [query, page]);

  return (
    <div>
      {isLoading && <Loader />} {/* Afieaz Loader dac se ncarc datele */}
      {error && <p>{error}</p>} {/* Afieaz mesajul de eroare dac exist */}
      <ul className={styles.gallery}>
        {images.map((image, index) => (
          <ImageGalleryItem
            key={image.id}
            images={images}
            image={image}
            index={index}
          />
        ))}
      </ul>
      {images.length > 0 && !isLoading && (
        <button
          className={styles.loadMoreButton}
          onClick={() => {
            setIsLoading(true); // Seteaz starea de ncrcare la true cnd se face clic pe buton
            setPage(prevPage => prevPage + 1);
          }}
        >
          Load More
        </button>
      )}
    </div>
  );
}
