import React, { useState } from 'react'; // Import React i useState pentru gestionarea strii
import Searchbar from './Searchbar/Searchbar'; // Import componenta Searchbar
import ImageGallery from './ImageGallery/ImageGallery'; // Import componenta ImageGallery

export const App = () => {
  const [query, setQuery] = useState(''); // Definete starea query pentru a stoca termenul de cutare

  // Funcia handleSearchSubmit este apelat atunci cnd formularul de cutare este trimis
  const handleSearchSubmit = event => {
    event.preventDefault(); // Previne comportamentul implicit al formularului (rencrcarea paginii)
    const form = event.currentTarget; // Obine referina la formularul care a declanat evenimentul
    const searchQuery = form.elements.search.value.trim(); // Obine valoarea introdus n cmpul de cutare i elimin spaiile albe de la nceput i sfrit
    if (searchQuery && searchQuery !== query) {
      // Verific dac valoarea introdus nu este goal i este diferit de termenul de cutare curent
      setQuery(searchQuery); // Actualizeaz starea query cu noul termen de cutare
    }
  };

  return (
    <div
      style={{
        display: 'grid', // Seteaz afiarea ca grid
        gridTemplateColumns: '1fr', // Definete o coloan n grid
        gap: '16px', // Seteaz spaiul dintre elementele grid-ului
        paddingBottom: '24px', // Seteaz padding-ul de jos
      }}
    >
      <Searchbar onSubmit={handleSearchSubmit} />{' '}
      {/* Afieaz componenta Searchbar i i transmite funcia handleSearchSubmit ca proprietate */}
      <ImageGallery query={query} />{' '}
      {/* Afieaz componenta ImageGallery i i transmite termenul de cutare query ca proprietate */}
    </div>
  );
};
